.about-header-text{
    position: absolute;
    z-index: 10000;
    top: 8rem;
    left: 3rem;

}
.header-text{
    font-size: 50px;
    font-weight: bold;
    width:30rem;
    max-width: 100%;
    color: #1f373d;
}
.about-input-search{
    border-radius: 0.25rem 0rem 0rem 0.25rem !important;
    background-color: #fff !important;
    height: 3rem !important;
    max-height: 3rem !important;
    margin-left: 1rem !important;
}
.about-search{
    color: #1f373d !important;
    font-weight: bold !important;
    border-radius: 0px 0.25rem 0.25rem 0px !important;
    background-color: #ffcc01 !important;
    height: 3rem !important;
    max-height: 100% !important;
}
.input-search-icon{
    position: absolute;
    bottom: 1rem;
    left: 2rem;
    color: #93a0b3;
}

.path-logo{
    width:900px;
    max-width: 100%;
}

.in-image-box-component{
    position:absolute;
    z-index: 10000;
    bottom: 1rem;
    right: 2.2rem;
}
.box-component-text{
    background-color: #ffcc01;
    padding: 1rem 5rem 1.1rem 5rem;
    color: #1f373d;
    font-weight: bold;
}

.about-font-icons{
    margin-right: 1rem;
    color: #fff;
}

.statistic-box{
    background-color: #f6f5f4;
    margin: 1rem;
    height: 11rem;
    max-height: 100%;
    max-width: 100% !important;

}
.statistic-box-center{
    text-align: center;
    margin-top: 3rem;
}
.statistic-box-color{
    color: #ffcc01;
    font-weight: bold;
    font-size: 2.4rem
}

.statistic-box-text-color{
    color: #1f373d;
    font-weight: bold;
}
.mt-10{
    margin-top: 10rem;
}
.company-text{
    font-weight: bold;
    color: #1f373d;
    width: 20rem;
    max-width: 100%;
    margin-bottom: 3rem;
    font-size: 40px;
}