.ap-header-text{
    width:27rem;
    max-width: 100%;
    color: #1f373d;
    font-weight: bold;
    font-size: 40px;
}

.box-bottom{
    background-color: #f2f1f0;
    padding: 1.5rem;
}
.box-text{
    color: #1f373d;
    font-weight: bold;
}
.box-price{
    font-weight: bold;
    color: #ffcc01;
}

.box-shadow{
    box-shadow: 0px 0px 3rem -2.5rem black
}
.img-size{
    width: 550px;
    max-width: 100%;
}
.box-place{
    display:flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 5px;
}
.app-i-color{
    color: #787878;
}
.btn-appartments{
    height: 3rem;
    max-height: 100%;
    margin-left: 2.2rem;
}
.fas.fa-search.apps-search{
    position: relative;
    left: 50px;
    top: 3px;
    color: #93a0b3;
}