.works-boxs-white{
    background-color: #fff;
    width: 345px;
    max-width: 100%;
    height: 374px;
    max-height: 100%;
    box-shadow: 0px 0px 3rem -2.5rem black;
}
.works-boxs-yellow{
    background-color: #ffcc01;
    color: #fff;
    width: 345px;
    max-width: 100%;
    height: 374px;
    max-height: 100%;
    box-shadow: 0px 0px 3rem -2.5rem black;
}


.img-position{
    position: absolute;
    top: -0.266px;
    left: 0;
}

.works-text{
max-width: 100%;
line-height: 30px;
}

.howitwork-row{
    flex-flow: inherit !important;
    margin-top: 5rem;
}

.works-box-position{
    position: relative;
    top: 10rem;
    left: 4rem;
    inline-size: 15rem;
    line-height: 2rem;
}
.works-box-span{
    position: relative;
    right: 1.4rem;
    font-weight:bold;
}

.find-location{
    text-decoration: none;
    color: #fff;
    font-weight:600;
}

.find-location:hover{
    text-decoration: none;
    border-bottom: 2px solid;
    color: #fff;
    font-weight:600;
}

.find-apps{
    text-decoration: none;
    color: #1f373d;
    font-weight:600;
}
.find-apps:hover{
    text-decoration: none;
    color: #1f373d;
    border-bottom: 2px solid;
    font-weight:600;
}